<template>
	<div id="project">
		<Navigation></Navigation>
    <div class="content">
      <div class="header">
        <!--搜索栏-->
        <div class="searchpatient">
          <input type="text"/>
          <!--<span class="searchButton"></span>-->
        </div>
        <!--end-->
        <!--面包屑导航条 当前位置-->
        <div class="breadcrumb">
          <a href="#/home"><i>首页</i><span>&gt;</span></a>
          <a href="javascript:;"><i>{{projectName}}</i><span>&gt;</span></a>
          <a href="#"><i></i><span>&gt;</span></a>
          <a href="javascript:;"><i></i><span>&gt;</span></a>
        </div>
      </div>
      <!--侧边导航栏-->
      <ul class="left-menu">
        <li>
          <router-link :to="'/project/'+projectid+'/all_patients'">患者病历</router-link>
        </li>
        </li>
        <li>
          <router-link :to="'/project/'+projectid+'/project_management'">项目管理</router-link>
        </li>
        <li>
          <router-link :to="'/project/'+projectid+'/follow_plans'">随访计划</router-link>
        </li>
        <!--end-->
      </ul>
		  <!--end-->
      
      <router-view class="show-area"></router-view>

    </div>
	</div>
</template>
<script>
  import Navigation from '../components/navigation.vue'
  import $ from 'webpack-zepto'
  import API from '../api.js'
  export default {
    data () {
      return {
        projectid: '',
        projectName: '',
        patientName: '',
        followName: ''
      }
    },
    components: {
      Navigation
    },
    created () {
      // 获取projectid
      this.projectid = this.$route.params.projectid
      // 项目详情
      API.GetProjectInfo({userId: this.$root.userid, projectId: this.$route.params.projectid}).then((response) => {
        // success callback
        this.projectName = response[0].u_projectName
        this.currentCatalogue()
        // console.log(response)
      }).catch((err) => {
        // error callback
        window.alert(err)
      })
      // 面包屑导航 的 patientname
      if (this.$route.params.MDID) {
        this.patientName = ''
        API.GetPatientBase({mdid: this.$route.params.MDID}).then((response) => {
          this.patientName = response[0].u_patientname
          this.currentCatalogue()
        }).catch((err) => {
          window.alert(err)
        })
      }
       // 面包屑导航 的 followname
      if (this.$route.params.followid) {
        this.followName = ''
        API.FollowDetail({userid: this.$root.userid, followid: this.$route.params.followid}).then((response) => {
          this.followName = response.u_follow_name
          this.currentCatalogue()
        }).catch((err) => {
          window.alert(err)
        })
      }
    },
    watch: {
      '$route': function (val) {
        this.currentCatalogue()
        if (this.$route.params.MDID) {
          this.patientName = ''
          API.GetPatientBase({mdid: this.$route.params.MDID}).then((response) => {
            this.patientName = response[0].u_patientname
            this.currentCatalogue()
          }).catch((err) => {
            window.alert(err)
          })
        }
        if (this.$route.params.followid) {
          this.followName = ''
          API.FollowDetail({userid: this.$root.userid, followid: this.$route.params.followid}).then((response) => {
            this.followName = response.u_follow_name
            this.currentCatalogue()
          }).catch((err) => {
            window.alert(err)
          })
        }
      }
    },
    methods: {
      /* 当前目录位置 面包屑导航条 */
      currentCatalogue () {
        if (this.$route.path.indexOf('/patient_detail/') !== -1) {
          // console.log(this.$route.params.MDID)
          // 进入患者病历详情页
          $('.breadcrumb a:last-child').show()
          $('.breadcrumb a:last-child').addClass('active').siblings().removeClass('active')
          $('.breadcrumb a:last-child').find('i').html(this.patientName)
          $('.breadcrumb a').eq(2).attr('href', '#' + this.$route.path.split('/patient_detail/')[0])
        } else if (this.$route.path.indexOf('/follow_detail/') !== -1) {
          // console.log(this.$route.params.MDID)
          // 进入随访计划详情页
          $('.breadcrumb a:last-child').show()
          $('.breadcrumb a:last-child').addClass('active').siblings().removeClass('active')
          $('.breadcrumb a').eq(2).attr('href', '#' + this.$route.path.split('/follow_detail/')[0])
          $('.breadcrumb a:last-child').find('i').html(this.followName)
        } else {
          $('.breadcrumb a:last-child').hide()
          $('.breadcrumb a').eq(2).addClass('active').attr('href', '#' + this.$route.path)
        }
        setTimeout(function () {
          $('.breadcrumb a').eq(2).find('i').html($('.left-menu li').find('.router-link-active').html())
        }, 200)
      }
    }
  }
</script>
<style scoped>
  .content{
    width: 90%;
    margin: 64px auto;
    padding-top: 20px;
  }
   /*侧边导航栏*/
  .left-menu{
    width: 180px;
    position: absolute;
    background: #fafafa;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, .13);
    font-size: 18px;
    padding: 10px 0px;
  }
  .left-menu li{
    line-height: 1.7;
    position: relative;
  }
  .left-menu li a{
    display: block;
    width: 100%;
    padding-left: .3em;
    position: relative;
    padding: 3px 10px;
    border-left: 3px solid #fafafa;
  }
  .left-menu li a i{
    cursor: pointer;
  }
  .left-menu li a.router-link-active{
    border-left-color: rgb(70, 141, 241);
    box-shadow: 0px 0px 10px 3px rgba(0, 0, 0, .2);
    z-index: 1
  }
  .groupList{
    margin-left: 1em;
  }
  .header{
    margin-bottom: 20px;
  }
  /* 搜索框 */
  .searchpatient input{
    width: 180px;
    padding: 1px;
    outline: none;
    border: none;
    border: 1px #ccc solid;
    border-radius: 3px;
    position: absolute;
  }
  /*面包屑导航条*/
  .breadcrumb{
    width: 1000px;
    margin-left: 220px;
    font-size: 17px;
    padding: 1px 0;
  }
  .breadcrumb a.active{
    color: #f00;
  }
  .breadcrumb a.active span{
    display: none;
  }
  .breadcrumb a span{
    margin-left: .5em;
  }
  /*病例展示区域style*/
  .show-area{
    width: 1000px;
    margin-left: 220px;
    letter-spacing: .1em;
    background: #fff;
    padding: 10px;
  }
</style>


