<template>
  <div style="background: rgb(238, 238, 238); padding: 0;">
    <div class="patient-detail" id="patient-detail">
      <div class="patientbase card">
        <p><span>姓名： </span><span v-text="patientBase.u_patientname"></span></p>        
        <p><span>性别： </span><span v-text="patientBase.u_gender == '' ? '' : patientBase.u_gender == '1' ? '男' : '女'"></span></p>
        <p><span>出生日期： </span><span v-text="patientBase.u_birthday"></span></p>
        <p><span>入组时间： </span><span v-text="patientBase.u_jointime"></span></p>        
        <p><span>创建人： </span><span v-text="patientBase.s_username"></span></p>
        <p><span>创建时间： </span><span>{{patientBase.u_createtime | formatDate}}</span></p>
        <p><span>所属项目： </span><span v-text="patientBase.u_projectName"></span></p>
      </div> 
      <div class="patient-records card" v-for="item in recordsData">
        <div class="card-header flex-row" style="justify-content: space-between; align-items: center;">
          <span>{{ patientBase.u_patientname + ' > ' + item.templatename}}</span>
          <span style="font-size: 12px; color: #777;">{{item.createtime | formatDate }}</span>
        </div>
        <div class="card-body">
          <p v-for="(value, key) in item.patientdata">{{ key + '： ' + value }}</p>
        </div>
        <div class="card-remark flex-row" v-if="item.remark != ''" >
          <p>{{ item.remark }}</p>
        </div>
        <div class="card-imgs flex-row">
          <template v-for="img in item.images" >
            <img class="small-img" :src="'http://' + img.url" @click="img.zoom = true"/>
            <div class="imgDialog" v-if="img.zoom" @click="img.zoom = false">
              <img class="large-img" :src="'http://'+ img.url"/>
            </div>
          </template>
        </div>
      </div>
      <div class="card" v-if="noRecordsDataFlag">
        <div class="card-header flex-row" style="justify-content: space-between; align-items: center;">
          <span>{{ patientBase.u_patientname + ' > ' + currentTemplateName}}</span>
        </div>
        <div class="card-body" style="color: #ccc"><p>此项记录暂无数据！</p></div>
      </div> 
    </div>
    <div class="template-menu" :style="{left: left + 'px', top: top + 'px'}">
      <p v-for="item in templates" :class="{active: item.u_templateid === currentTemplateId}" v-text="item.u_templatename" @click.stop="changeTemplateData(item.u_templateid, item.u_templatename)"></p>
    </div>  
  </div>
</template>
<script>
  import API from '../api.js'
  export default {
    data () {
      return {
        // template-menu position
        left: '',
        top: '',
        patientBase: {},
        templates: [],
        currentTemplateId: '',
        currentTemplateName: '',
        noRecordsDataFlag: false,
        recordsData: []
      }
    },
    mounted () {
      // template-menu 的left 和 top 值
      this.left = document.getElementById('patient-detail').offsetLeft + document.getElementById('patient-detail').offsetWidth + 20
      this.top = document.getElementById('patient-detail').offsetTop
      window.addEventListener('resize', () => {
        this.left = document.getElementById('patient-detail').offsetLeft + document.getElementById('patient-detail').offsetWidth + 20
      }, false)
      this.getPatientBase()
      this.getProjectTemplate()
    },
    methods: {
      /* patientBase */
      getPatientBase () {
        API.GetPatientBase({mdid: this.$route.params.MDID}).then((response) => {
          this.patientBase = response[0]
        }).catch((err) => {
          window.alert(err)
        })
      },
      /* projectTemplate */
      getProjectTemplate () {
        API.GetTemplates({projectid: this.$route.params.projectid}).then((response) => {
          this.templates = response
          this.changeTemplateData(this.templates[0].u_templateid, this.templates[0].u_templatename)
        }).catch((err) => {
          window.alert(err)
        })
      },
      /* 点击template-menu show对应的records */
      changeTemplateData (templateid, templatename) {
        this.currentTemplateId = templateid
        this.currentTemplateName = templatename
        this.recordsData = []
        API.GetPatientRecords({mdid: this.$route.params.MDID, templateid: templateid}).then((response) => {
          if (response.length === 0) {
            this.noRecordsDataFlag = true
            return
          }
          this.noRecordsDataFlag = false
          response.map(n => this.recordsData.push({
            templatename: n.u_templatename,
            createtime: n.u_createtime,
            patientdata: JSON.parse(n.u_patientdata),
            /* eslint-disable */
            images: eval('{'+ n.imgs +'}'),
            /* eslint-endable */
            remark: n.remark // 长文本
          }))
          // 添加图片放大的属性
          for(let key in this.recordsData) {
            let images = this.recordsData[key].images
            this.recordsData[key].images = []
            for(let i in images) {
              this.recordsData[key].images.push({
                url: images[i],
                zoom: false //放大图片
              })
            }
          }
        }).catch((err) => {
          console.log(err)
        })
      }
    }
  }
</script>
<style scoped>
  /* 基本信息 */
  .patient-detail {
    margin-right: 250px;
    width: 750px;
    font-size: 14px;
  }
  .patientbase{
    padding-left: 12px;
  }
  .card{
    border: 1px #ccc solid;
    background: #fff;
    margin-bottom: 20px;
  }
  .card p {
    line-height: 2;
  }
  .card-header {
    border-bottom: 1px #eee solid;
    padding: 8px 12px;
  }
  .card-body {
    background: #fafafa;
    margin: 12px;
    border: 1px #e0e0e0 solid;
    border-radius: 3px;
    padding-left: 12px;
  }
  .card-remark{
    padding: 4px 20px;
    margin: 10px 0 12px;
    border-left: 3px solid #3cb6ec;
    line-height: 2;
    font-size: 16px;
    color: rgba(0,0,0,.64);
  }
  .card-imgs .small-img {
    width: 80px;
    height: 80px;
    margin-left: 12px;
    margin-bottom: 10px;
    cursor: pointer;
  }
  .imgDialog{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    background: rgba(0,0,0,.5);
    width: 100%;
    height:  100%;
    display: flex;
    justify-content: center;
    align-items: center; 
  }
  .card-imgs .large-img{
    width: 600px;
  }
  .template-menu {
    width: 230px;
    background: #fff;
    position: fixed;
    top: 30%;
    border: 1px #ccc solid;
  }
  .template-menu p{
    border-bottom: 1px #eee solid;
    border-left: 3px #fff solid;
    padding: 10px 20px;
    font-size: 14px;  
    cursor: pointer;
  }
  .template-menu p.active {
    border-left-color: #3bb5ed;
  }
</style>


