<template>
  <div id="home">
    <Navigation></Navigation>
    <div class="content">
      <div class="tools">
        <MButton class="btn" :type="mytype" @click="getMyProjects">我的项目</MButton>
        |
        <MButton class="btn" :type="alltype" @click="getAllProjects">所有公开的项目</MButton>
      </div>
      <!--用户没有参与的项目提示-->
      <div class="no-join-project" v-if="myProjectList.length === 0 && my">
        <p>您好，目前您还没有参加任何项目！</p>
        <p><span @click="getAllProjects">点击查看</span>所有公开项目</p>
      </div>
      <!--用户参加的项目卡片列表-->
      <div class="project-card" v-for="item in myProjectList" v-if="my">
        <router-link :to="'/project/' + item.u_projectID + '/all_patients/1'">
          <div class="project-text">
            <h3 v-text="item.u_projectName"></h3>
            <p v-text="item.u_projectMem"></p>
          </div>
        </router-link>
        <div class="add-project-status">
          <span>已加入项目</span>
        </div>
      </div>
      <!--所有公开的项目卡片列表-->      
      <div class="project-card" v-for="item in allProjectList" v-if="!my">
        <div class="project-text">
          <h3 v-text="item.u_projectName"></h3>
          <p v-text="item.u_projectMem"></p>
        </div>
        <div class="add-project-status">
          <span style="cursor: pointer;" v-if="!item.is_join" @click="applyJoinProject(item.u_projectID)">申请加入项目</span>
          <span v-else>已加入项目</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import API from '../api.js'
  import Navigation from '../components/navigation.vue'
  import MButton from '../lib/button.vue'
  export default {
    name: 'Home',
    data () {
      return {
        allProjectList: [],
        myProjectList: [],
        my: false,
        mytype: 'blue',
        alltype: 'gray'
      }
    },
    components: {
      Navigation,
      MButton
    },
    created () {
      this.getMyProjects()
    },
    methods: {
      // 获取用户参与的项目
      getMyProjects () {
        this.mytype = 'blue'
        this.alltype = 'gray'
        this.my = true
        this.myProjectList = []
        API.GetMyProjects({userid: this.$root.userid}).then(response => {
          response.map(n => this.myProjectList.push(n))
        }).catch(err => {
          console.log(err)
        })
      },
      // 获取所有公开项目
      getAllProjects () {
        this.mytype = 'gray'
        this.alltype = 'blue'
        this.my = false
        this.allProjectList = []
        API.GetAllProjects().then(response => {
          response.map(n => {
            this.allProjectList.push({
              u_projectMem: n.u_projectMem,
              u_projectID: n.u_projectID,
              u_projectName: n.u_projectName,
              is_join: false
            })
          })
          this.checkIsJoin()
        }).catch(err => {
          console.log(err)
        })
      },
      // 判断用户已加入的项目
      checkIsJoin () {
        for (let i in this.allProjectList) {
          if (this.myProjectList.length === 0) { return }
          for (let j in this.myProjectList) {
            this.myProjectList[j].u_projectID === this.allProjectList[i].u_projectID ? this.allProjectList[i].is_join = true : ''
          }
        }
      },
      // 申请加入项目
      applyJoinProject (projectID) {
        API.ApplyProject({userid: this.$root.userid, projectid: projectID}).then(response => {
          this.toast({
            type: 'success',
            text: '加入申请已发送给项目管理员',
            placement: 'top'
          })
        }).catch(error => {
          let type = error === '此人已在项目中' ? '' : 'error'
          error = error === '此人已在项目中' ? '您已在项目中' : '申请失败，请重新尝试'
          this.toast({
            type: type,
            text: error,
            placement: 'top'
          })
        })
      }
    }
  }
</script>
<style scoped>
  #home .content {
    width: 80%;
    margin: 100px auto;
  }
  /* 按钮工具 */
  .content .tools {
    text-align: center;
    margin: 50px 0 20px;
  }
  .content .tools .btn{
    margin: 0 20px;
  }

  /* 项目卡片 */
  .content .project-card {
    width: 22%;
    margin: 10px 1.5%;
    background: #fff;
    line-height: 1.7;
    border: 1px rgb(207,207,207) solid;
    float: left;
  }
  .content .project-card:hover{
    box-shadow: 0 0 5px 5px #ccc;
  }
  .project-card .project-text {
    padding: 12px;
  }
  .project-card .add-project-status {
    border-top: 1px solid rgb(227, 227, 227);
    padding: 12px;
    font-size: 14px; 
  }
  /* 没有参加项目 */
  .no-join-project {
    text-align: center;
    color: rgb(68,68, 68);
    margin-top: 100px;
    font-size: 18px;
  }
  .no-join-project span {
    text-decoration: underline;
    color: #f00;
  }
  
  
</style>



