<template>
  <div id="nav">
    <div class="flex-row">
      <img src="../assets/logo.png" alt="Medicayun" class="logo">
      <router-link to='/home' style="padding-left: 20px; color: #fff;">首页</router-link>
    </div>
    <div class="flex-row right">
      <router-link to='/custom_form' style="padding-left: 20px; color: #fff;">自定义form</router-link>
      <router-link to='/notification' class="iconfont icon-tongzhi1" tag="i"><span v-if="nodispose" class="notice"></span></router-link>
      <i class="iconfont icon-menu" @click.stop="changeCloud"></i>
      <div class="user-img" @click.stop="showConfig">
        <template v-if="userInfo.s_avatar">
          <img src="userInfo.s_avatar" alt="userImg">
        </template>
        <span v-else>{{shortName | uppercase}}</span>
      </div>
      <Popover ref="config" class="config flex-col">
        <router-link to="/user/userInfo" class="user-info flex-row">
          <span class="user-img large">{{ shortName | uppercase }}</span>
          <div class="flex-col" style="flex: 1;">
            <span class="user-name">{{ userInfo.s_username }}</span>
            <span class="user-work-unit">{{ userInfo.s_workunti }}</span>
          </div>
        </router-link>
        <div class="link-list flex-col">
          <router-link to="/user/userInfo" class="link-item">用户设置</router-link>
          <router-link to="/user/userInfo" class="link-item">用户帮助</router-link>
        </div>
        <div class="exit-btn"><MButton type="blue" @click="exit">退出</MButton></div>
      </Popover>
      <popover ref="cloud" class="cloud">
        <div class="flex-col">
          <i class="iconfont icon-beizhu" style="font-size: 42px;color: orange;"></i>
          <span style="font-size: 13px;text-align: center;">MedNotes</span>
        </div>
        <div class="flex-col">
          <i class="iconfont icon-xiangmu" style="font-size: 38px;color: blue;"></i>
          <span style="font-size: 13px;text-align: center;">云管理</span>
        </div>
        <div class="flex-col">
          <i class="iconfont icon-xiangmu1" style="font-size: 39px;color: blue;"></i>
          <span style="font-size: 13px;text-align: center;">云影像</span>
        </div>
      </popover>
    </div>
  </div>
</template>
<script>
  import API from '../api.js'
  import MButton from '../lib/button.vue'
  import Popover from '../lib/Popover.vue'
  export default {
    name: 'Nav',
    data () {
      return {
        userInfo: {},
        shortName: '',
        nodispose: false // 新消息提醒
      }
    },
    components: {
      MButton,
      Popover
    },
    mounted () {
      this.getNoticeList()
      this.getUserInfo()
    },
    methods: {
      // 获取通知列表
      getNoticeList () {
        API.GetNotices({userid: this.$root.userid}).then(response => {
          if (!response.length) { return }
          for (let n in response) {
            if (n.status === '0') {
              this.nodispose = true
              break
            }
          }
        }).catch(err => {
          window.alert(err)
        })
      },
      // 获取用户信息
      getUserInfo () {
        API.GetUserInfo({userid: this.$root.userid}).then(response => {
          this.userInfo = response[0]
          this.$root.userInfo = this.userInfo
          this.nameToshort(this.userInfo.s_username)
        }).catch((err) => {
          window.alert(err)
        })
      },
      // 获取用户姓名首字母缩写
      nameToshort (str) {
        API.ChineseToShort({string: str}).then(response => {
          // console.log(response.shortpinyin)
          this.shortName = response.shortpinyin
          this.$root.shortName = this.shortName
        }).catch(err => {
          console.log(err)
        })
      },
      // 显示用户配置dialog
      showConfig (event) {
        this.$refs.config.show(event.currentTarget)
        this.$refs.cloud.hide()
      },
      // 显示云dialog
      changeCloud (event) {
        this.$refs.cloud.show(event.currentTarget)
        this.$refs.config.hide()
      },
      /* 退出 */
      exit () {
        this.$router.push('/login')
        window.sessionStorage.clear()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* 导航条固定定位 */
  #nav {
    background: rgb(70, 141, 241);
    width: 100%;
    height: 64px;
    padding: 0 32px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 18px;
    color: #fff;    
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
  }
  
  .logo {
    width: 151px;
    height: 45px;
    padding-right: 20px;
    border-right: 1px #ddd solid;
  }
  .flex-row{
    align-items: center;
    height: 100%;
  }
  .right i.iconfont{
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    font-weight: 600;
    padding: 8px 10px;
    position: relative;
  }
 .right i.iconfont:hover {
    background: rgba(255, 255, 255, .2);
    border-radius: 50%;
  }
  .notice{
    position: absolute;
    display: block;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background: #f00;
    right: 7px;
    top: 5px;
  }
  .right .user-img {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background: #fff;
    text-align: center;
    line-height: 32px;
    cursor: pointer;
    margin-left: 12px;
    font-size: 12px;
    color: #000;
  }

  .config .user-info{
    height: 102px;
    padding: 0 12px;
    border-bottom: 2px solid rgb(227, 227, 227);
    background: rgb(250, 250, 250);
    line-height: 24px;
  } 
  .config .user-info .large{
    width: 70px;
    height: 70px;
    background: rgb(70, 141, 241);
    line-height: 70px;
    color: #fff;
    font-size: 22px;
    margin-right: 12px;
  }
  .config .user-info .user-name {
    font-size: 18px;
    height: 24px;
    line-height: 24px;
  }
  .config .user-info .user-work-unit {
    overflow : hidden;
    width: 140px;
    font-size: 14px;
  }
  .config .link-list{
    font-size: 18px;
    color: rgb(68, 68, 68);
    line-height: 30px;
    margin-left: 24px;
    margin-right: 12px;
    padding: 12px;
    border-bottom: 1px solid rgb(227, 227, 227);
  }
  .config .link-list a:hover{
    color: rgb(70, 141, 241)
  }
  .config .exit-btn {
    padding: 12px;
    text-align: right;
  } 
  
  .cloud{
   padding: 28px;
   width: 320px;
   display: flex;
 }

 .cloud>div {
   flex: 1;
   justify-content: center;
   align-items: center;
   cursor: pointer;
 }

 .cloud i {
   display: inline-block;
   /*height: 64px;*/
   /*width: 64px;*/
   line-height: 64px;
 }
</style>



