import Vue from 'vue'
import VueRouter from 'vue-router'
/** all link */
import Login from './views/Login'
import Home from './views/Home'
import Project from './views/Project.vue'
import AllPatients from './views/Allpatients.vue'
import PatientDetail from './views/Patientdetail.vue'
// import Records from './views/Records.vue'
import ProjectManagement from './views/Projectmanagement.vue'
import FollowPlan from './views/Followplan.vue'
import FollowDetail from './views/Followdetail.vue'
import Notification from './views/Notification.vue'
import User from './views/User.vue'
import UserInfo from './views/Userinfo.vue'
import ResetAvatar from './views/Resetavatar.vue'
import ResetPw from './views/Resetpw.vue'
import CustomForm from './views/CustomForm.vue'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'hash',
  routes: [
    {
      path: '/',
      component: Home,
      meta: {requiresAuth: true, title: '首页'}
    },
    {
      path: '/home',
      component: Home,
      meta: {requiresAuth: true, title: '首页'}
    },
    {
      path: '/project/:projectid',
      component: Project,
      meta: {requiresAuth: true},
      children: [
        {
          path: '/project/:projectid/all_patients',
          component: AllPatients,
          meta: {requiresAuth: true, title: '患者病例列表'}
        },
        {
          path: '/project/:projectid/all_patients/:pagenum',
          name: 'patientList',
          component: AllPatients,
          meta: {requiresAuth: true, title: '患者病例列表'}
        },
        {
          path: '/project/:projectid/all_patients/:pagenum/patient_detail/:MDID',
          component: PatientDetail,
          meta: {requiresAuth: true, title: '患者详情'}
        },
        {
          path: '/project/:projectid/project_management',
          component: ProjectManagement,
          meta: {requiresAuth: true, title: '项目管理'}
        },
        {
          path: '/project/:projectid/follow_plans',
          component: FollowPlan,
          meta: {requiresAuth: true, title: '随访计划列表'}
        },
        {
          path: '/project/:id/follow_plans/follow_detail/:followid',
          component: FollowDetail,
          meta: {requiresAuth: true, title: '随访计划管理'}
        }
      ]
    },
    {
      path: '/custom_form',
      component: CustomForm,
      meta: {requiresAuth: true, title: '自定义表单'}
    },
    {
      path: '/notification',
      component: Notification,
      meta: {requiresAuth: true, title: '通知'}
    },
    {
      path: '/user',
      component: User,
      meta: {requiresAuth: true, title: '用户'},
      children: [
        {
          path: '/user/userInfo',
          component: UserInfo,
          meta: {requiresAuth: true, title: '个人信息'}
        },
        {
          path: '/user/resetAvatar',
          component: ResetAvatar,
          meta: {requiresAuth: true, title: '头像修改'}
        },
        {
          path: '/user/resetPw',
          component: ResetPw,
          meta: {requiresAuth: true, title: '密码修改'}
        }
      ]
    },
    {
      path: '/login',
      component: Login,
      meta: {title: '登录'}
    },
    { path: '*', component: Login }
  ]
})

export default router



