<template>
  <div class="chang-pw">
    <h2>修改密码</h2>
    <div class="option flex-row">
      <label style="width: 180px;">当前密码：</label>
      <input type="password" v-model="oldPw"/>
    </div>
    <div class="option flex-row">
      <label style="width: 180px;">输入新密码：</label>
      <input type="password" v-model="newPw" />
    </div>
    <div class="option flex-row">
      <label style="width: 180px;">再次输入新密码：</label>
      <input type="password" v-model="againPw" />
    </div>
    <div class="option flex-row">
      <span style="width: 180px;"></span>
      <div class="tools">
        <m-button type="blue" @click="changePw">确定</m-button>
        <m-button type="gray" @click="cancelChange">取消</m-button>
      </div>
    </div>
  </div>
</template>
<script>
  import API from '../api.js'
  import MButton from '../lib/button.vue'
  export default {
    name: 'Profile',
    components: {
      MButton
    },
    data () {
      return {
        oldPw: '',
        newPw: '',
        againPw: ''
      }
    },
    methods: {
      cancelChange () {
        this.oldPw = ''
        this.newPw = ''
        this.againPw = ''
      },
      changePw () {
        API.ResetPassWord({userid: this.$root.userid, oldpassword: this.oldPw, newpassword: this.newPw, repassword: this.againPw}).then((response) => {
          this.toast({
            text: '修改成功',
            type: 'success',
            placement: 'top'
          })
        }).catch(err => {
          this.toast({
            text: '保存失败，重新提交试试',
            type: 'error',
            placement: 'top'
          })
          console.log(err)
        })
      }
    }
  }
</script>
<style scoped>

 .chang-pw {
   background: #fff;
   padding: 24px;
   flex: 1;
   border: 1px solid #ccc;
   align-items: center;
 }

  .chang-pw h2 {
    border-bottom: 1px solid #eee;
    font-size: 22px;
    font-weight: 400;
    color: #777;
    padding-bottom: 24px;
  }
 .option {
   padding: 10px 0;
 }
 .option>label{
    line-height: 32px;
  }

  .chang-pw input[type="password"] {
    height: 30px;
    line-height: 30px;
  }

  .tools{
    width: 100%;
    text-align: left;
    margin: 12px 5px;
    cursor: pointer;
  }
  .tools>span:first-child{
    margin-right: 24px;
  }
</style>


