<template>
  <div id="followUp">
    <MButton :type="'blue'" @click="createPlanDialog = true">创建新随访计划</MButton>
    <DialogBox v-if="createPlanDialog">
      <h3 slot="header">创建新随访计划</h3>
      <div slot="body" class="body">
        <div class="form-item">
          <div class="label">
            <i class="must">*</i>
            <label>计划名称：</label>  
          </div>
          <input type="text" v-model="planName"/>
        </div>
        <div class="form-item">
          <div class="label">
            <i class="must">*</i>
            <label>计划简介：</label>
          </div>
          <textarea v-model="planMem"></textarea>
        </div>
        <div class="form-item">
          <div class="label">
            <i class="must">*</i>
            <label>开始日期：</label>  
          </div>
          <div class="input" @click="openPicker('picker1', $event)">
            <input type="text" v-model="calendar.pickers.picker1.value" placeholder="请输入日期">
            <i class="iconfont rili" :class="{'icon-rili': calendar.pickers.picker1.riliFlag, 'icon-x': calendar.pickers.picker1.xFlag}" @mouseover="mouseoverIcon('picker1')" @mouseout="mouseoutIcon('picker1')" @click.self="clearDate('picker1')"></i>            
          </div>
        </div>
        <!--日期面板-->
        <calendar
          :show="calendar.show"
          :value="calendar.value"
          :x="calendar.x"
          :y="calendar.y"
          :picker='calendar.picker'
          @select="select">
        </calendar>
        <div class="form-item">
          <div class="label">
            <i class="must">*</i>
            <label>结束日期：</label>
          </div>
          <div class="input" @click="openPicker('picker2', $event)">
            <input type="text" v-model="calendar.pickers.picker2.value" placeholder="请输入日期">          
            <i class="iconfont rili" :class="{'icon-rili': calendar.pickers.picker2.riliFlag, 'icon-x': calendar.pickers.picker2.xFlag}" @mouseover="mouseoverIcon('picker2')" @mouseout="mouseoutIcon('picker2')" @click.self="clearDate('picker2')"></i>
          </div>
        </div>
        <div class="form-item">
          <div class="label">
            <i class="must">*</i>
            <label>时间间隔：</label>
          </div>
          <select v-model="planInterval">
            <option value="1">一个月</option>
            <option value="3">三个月</option>
            <option value="6">半年</option>
            <option value="12">一年</option>
          </select>
        </div>
      </div>
      <div slot="footer" class="footer" style="text-align: right">
        <MButton :type="'gray'" @click="createPlanDialog = false">取消</MButton>        
        <MButton :type="'blue'" :loading="buttonLoading" @click="createPlan">确定</MButton>
      </div>
    </DialogBox>        
    <!--消息提示-->
    <NoticeMessage
      :show='notice.showMessage' 
      :message="notice.message" 
      :duration="notice.duration" 
      :showClose="notice.showClose" 
      :type="notice.type" 
      @close="notice.showMessage = false">
    </NoticeMessage>
    <!--<button @click="dianji('创建计划成功')">点击</button>-->
    <div class="follow-list-show">
      <div class="follow-list fontBold">
        <div class="follow-name">计划名称</div>
        <div class="follow-status">计划状态</div>
        <div class="follow-starttime">计划开始日期</div>
        <div class="follow-endtime">计划结束日期</div>
        <div class="follow-createtime">计划创建日期</div>
        <div class="follow-createname">计划创建人</div>       
        <div class="follow-handle">操作</div>
      </div>
      <div class="follow-list"  v-for="(item,index) in followInfo" :key="item.u_follow_id">
        <div class="follow-name"><router-link :to="'/home/projectid/'+ projectid + '/follow_plans/follow_detail/'+item.u_follow_id" v-text="item.u_follow_name"></router-link></div>
        <div class="follow-status" v-text="item.u_status">计划状态</div>
        <div class="follow-starttime" v-text="item.u_follow_start">计划开始日期</div>
        <div class="follow-endtime" v-text="item.u_follow_end">计划开始日期</div>
        <div class="follow-createtime" v-text="item.u_createtime.slice(0, 10)">计划创建日期</div>
        <div class="follow-createname" v-text="item.u_createname">计划创建人</div>          
        <div class="follow-handle">删除</div>
      </div>
    </div>
  </div>
</template>
<script>
  import DialogBox from '../lib/dialog.vue'
  import API from '../api.js'
  import calendar from '../components/calendar.vue'
  import NoticeMessage from '../components/noticeMessage.vue'
  import MButton from '../lib/button.vue'
  export default {
    data () {
      return {
        createPlanDialog: false,
        planName: '',
        planMem: '',
        planInterval: '',
        followInfo: [],
        projectid: this.$route.params.id,
        calendar: {
          show: false,
          value: new Date().toJSON().slice(0, 10),
          x: 0,
          y: 0,
          picker: '',
          pickers: {
            picker1: {
              value: '',
              riliFlag: true,
              xFlag: false
            },
            picker2: {
              value: '',
              riliFlag: true,
              xFlag: false
            }
          }
        },
        notice: {
          showMessage: false,
          message: '',
          duration: 10000,
          showClose: true,
          type: 'info'
        },
        buttonLoading: false
      }
    },
    components: {
      DialogBox,
      calendar,
      NoticeMessage,
      MButton
    },
    created () {
      this.getFollowList()
    },
    methods: {
      // 获取项目下的随访计划
      getFollowList () {
        API.GetFollows({userid: window.sessionStorage.getItem('userid')}).then((response) => {
          this.followInfo = response.map(item => item.uFollowData[0])
        }).catch((err) => {
          window.alert(err)
        })
      },
      // 创建新随访计划
      createPlan () {
        if (!this.buttonLoading) {
          this.buttonLoading = true
          API.CreateFollow({userid: window.sessionStorage.getItem('userid'), projectid: this.$route.params.id, followplan: JSON.stringify({'u_follow_name': this.planName, 'u_follow_mem': this.planMem, 'u_follow_start': this.calendar.pickers.picker1.value, 'u_follow_end': this.calendar.pickers.picker2.value, 'u_follow_setting': this.planInterval})}).then((response) => {
            this.createPlanDialog = false
            this.getFollowList()
            this.noticeMessage('创建成功')
            this.buttonLoading = false
          }).catch((err) => {
            this.buttonLoading = false
            if (typeof err === 'string') {
              this.noticeMessage(err, 'error')
            } else {
              this.noticeMessage('timeout of 5000ms exceeded', 'error')
            }
          })
        }
      },
      // 打开日期控制面板
      openPicker (picker, e) {
        // 设置类型
        if (this.calendar.picker === picker) {
          // 两次点击的事件源是同一个
          this.calendar.show = !this.calendar.show
        } else {
          this.calendar.show = true
        }
        this.calendar.picker = picker // 用于记录该事件源
        this.calendar.value = this.calendar.pickers[picker].value
        this.calendar.x = e.currentTarget.offsetLeft
        this.calendar.y = e.currentTarget.offsetTop + e.currentTarget.offsetHeight + 8
      },
      // 显示选中的日期  子组件触发
      select (value, picker) {
        this.calendar.show = false
        this.calendar.pickers[picker].value = value
      },
      // 日期框的不同图标显示  鼠标滑入
      mouseoverIcon (picker) {
        if (this.calendar.pickers[picker].value !== '') {
          this.calendar.pickers[picker].riliFlag = false
          this.calendar.pickers[picker].xFlag = true
        } else {
          this.calendar.pickers[picker].riliFlag = true
          this.calendar.pickers[picker].xFlag = false
        }
      },
      // 日期框的不同图标显示  鼠标滑出
      mouseoutIcon (picker) {
        this.calendar.pickers[picker].riliFlag = true
        this.calendar.pickers[picker].xFlag = false
      },
      // 清除该事件源的日期
      clearDate (picker) {
        if (this.calendar.pickers[picker].xFlag) {
          this.calendar.pickers[picker].value = ''
          this.calendar.pickers[picker].riliFlag = true
          this.calendar.pickers[picker].xFlag = false
        }
      },
      // 消息提示
      noticeMessage (message, type) {
        this.notice.message = message
        this.notice.showMessage = true
        this.notice.type = type
        this.notice.duration = 10000
      }
    }
  }
</script>
<style scoped>
  /* 创建计划对话框 */
  .form-item{
    font-size: 14px;
    margin-bottom: 5px;
    display: flex;
    display: -webkit-flex;
    align-items: center;
  }
  .must{
    color: #f00;
    position: absolute;
    left: -10px;
    top: 2px;
  }
  .form-item .label{
    width: 80px;
    position: relative;
  }
  .rili{
    position: absolute;
    color: #bbb;
    top: 15%;
    right: 3%;
  }
  .icon-x:before { content: "\e629"; }
  .icon-rili:before { content: "\e6eb"; }
  .input{
    flex:1;
    cursor: pointer;
    position: relative;
  }
  .form-item input, .form-item textarea, .form-item select{
    border: none;
    border: 1px #eee solid;
    border-radius: 3px;
    background: #fff;
    padding: 3px;
    flex: 1;
    width: 100%
  }
  .form-item select{
    padding: 8px;
  }
  /* 计划列表显示 */
  .fontBold{
    font-weight: 700;
  }
  .follow-list.fontBold:hover{
    background: #f4f6f8
  }
  .follow-list{
    border-bottom: 1px #eee solid;
    width: 100%;
    display: table;
    text-align: center;
    padding: .5em 0;
  }
  .follow-list:hover{
    background: #ccc;
  }
  .follow-name{
    display: table-cell;
    width: 10em;
  }
  .follow-name a{
    color: #20a0ff;
    text-decoration: underline;
  }
  .follow-status{
    display: table-cell;
    width: 6em;
  }
  .follow-starttime, .follow-endtime, .follow-createtime{
    display: table-cell;
    width: 8em;
  }
  .follow-createname{
    display: table-cell;
    width: 6em;
  }
  .follow-handle{
    display: table-cell;
    width: 4em;
    cursor: pointer;
  }
</style>


