<template>
  <div id="userInfo">
    <Navigation></Navigation>
    <div class="content flex-row">
      <div class="profile">
        <div class="user-info">
          <div class="avatar">
            <template v-if="$root.userInfo.s_avatar">
              <img src="$root.userInfo.s_avatar" alt="userImg">
            </template>
            <span v-else>{{ $root.shortName | uppercase }}</span>
          </div>
          <div class="text flex-col" style="flex: 1;">
            <span class="name">{{ $root.userInfo.s_username }}</span>
            <span class="work">{{ $root.userInfo.s_workunti }}</span>
          </div>
        </div>
        <div class="link-list flex-col">
          <router-link to="/user/userInfo" class="link-item active">个人信息</router-link>
          <router-link to="/user/resetAvatar" class="link-item">头像修改</router-link>
          <router-link to="/user/resetPw" class="link-item">密码修改</router-link>
        </div>
      </div>
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
  import Navigation from '../components/navigation.vue'
  export default {
    name: 'User',
    components: {
      Navigation
    }
  }
</script>
<style scoped>
  .content{
    width: 80%;
    margin: 114px auto;
    justify-content: center;
  }

  .profile {
    width: 250px;
    margin-right: 24px;
    height: 300px;
    background: #fafafa;
    /*padding: 24px 12px;*/
    box-shadow: 3px 3px 5px rgba(0, 0, 0, .13);
  }

  .profile .user-info {
    text-align: center;
    border-bottom: 1px solid #eee;
    margin: 24px 12px 0 12px;
  }

  .profile .avatar span{
    display: inline-block;
    font-size: 22px;
    height: 72px;
    width: 72px;
    line-height: 72px;
    background: #468df1;
    color: #fff;
    border-radius: 50%;
  }

  .profile .user-info .text {
    padding: 12px 0;
  }

  .profile .user-info .name {
    font-size: 18px;
    line-height: 24px;
  }

  .profile .user-info .work {
   font-size: 14px;
   line-height: 24px;
 }

 .profile .link-list {
   padding: 12px 0;
   font-size: 18px;
   line-height: 24px;
 }

 .profile .link-list a{
   font-size: 18px;
   line-height: 2;
   padding-left: 12px;
 }

 .profile .link-list a.router-link-active {
   background: #ccc;
 }

</style>


