import Vue from 'vue'
import Dialog from './dialog.vue'
import MButton from './button.vue'

const $body = document.querySelector('body')

const createNode = () => {
  const $node = document.createElement('div')
  $body.appendChild($node)
  return $node
}

const removeNode = $node => {
  $body.removeChild($node)
}

const confirm = (options) => {
  const {title, message, onConfirm, onHide, onShow} = options
  /* eslint-disable no-new */
  new Vue({
    el: createNode(),
    data () {
      return {
        show: false
      }
    },
    components: {
      Dialog,
      MButton
    },
    template: `<Dialog ref="dialog"
      @confirm="handleConfirm"
      @hide="handleHide"
      @show="handleShow"
      @closed="destroy">
      <div slot="header">${title}</div>
      <div slot="body">
        ${message}
      </div>
      <div slot="footer">
        <MButton type="gray" @click.native="destroy">取消</MButton>
        <MButton type="blue" @click.native="handleConfirm">确定</MButton>
      </div>
    </Dialog>`,
    mounted () {
      this.$nextTick(() => {
        this.$refs.dialog.open()
      })
    },
    destroyed () {
      removeNode(this.$el)
    },
    methods: {
      handleShow () {
        onShow && onShow()
      },
      handleConfirm () {
        onConfirm && onConfirm()
        this.$refs.dialog.close()
      },
      handleHide () {
        onHide && onHide()
      },
      destroy () {
        this.$destroy()
      }
    }
  })
}

export default confirm



