<template>
    <div id="all-patients">
        <div class="tools">
          <div class="tools-left">
            <MButton type="blue" @click="allChecked">全选/取消</MButton>            
            <MButton :class="{disabled: disabled}" type="blue" @click="deletePatients">批量删除</MButton>
          </div>
        </div>
        <div class="body">
          <div class="show-container">
              <div class="fontBold">
                <div>
                  <input type="checkbox" />
                  <label style="visibility: hidden;"></label>
                </div>
                <div style="visibility: hidden"><input type="checkbox" v-model="checkList"/><span><i></i></span></div>
                <div class="patient-name">姓名</div>
                <div class="patient-sex">性别</div>
                <div class="patient-age">年龄</div>
                <div class="patient-birthday">出生年月</div>
                <div class="join-time">入组时间</div>
                <div class="update-time">更新时间</div>
                <div class="creater">创建人</div>
                <div class="handle">操作</div>
              </div>
          </div>
          <div class="show-container"  v-for="(item,index) in currentPagePatiens" :key="item.u_MDID">
              <div class="recordSheet">
                  <div>
                    <input type="checkbox" v-model="checkList" :id="item.u_MDID" :value="item.u_MDID"/>
                    <label :for="item.u_MDID"></label>
                  </div>
                  <div class="patient-name"><router-link :to="'/project/'+ projectid + '/all_patients/' + currentPage +'/patient_detail/'+item.u_MDID" v-text="item.u_patientname"></router-link></div>
                  <div class="patient-sex" v-text="item.u_gender == '' ? '' : item.u_gender == '1' ? '男' : '女'"></div>
                  <div class="patient-age"></div>
                  <div class="patient-birthday" v-text="item.u_birthday"></div>
                  <div class="join-time" v-text="item.u_jointime"></div>
                  <div class="update-time" v-text="item.u_createtime"></div>
                  <div class="creater" v-text="item.s_username"></div>
                  <div class="handle" @click="deletePatient(item.u_MDID)">删除</div>
              </div> 
          </div>
        </div>
        <div style="padding: 10px 0;text-align: center;">
          <Page :total="allPatientNum" :current="currentPage" :pagesize="30" @change="changsite"></Page>
        </div>
    </div>
</template>
<script>
  // import $ from 'webpack-zepto'
  import DialogBox from '../lib/dialog.vue'
  import MButton from '../lib/button.vue'
  import Page from '../lib/page.vue'
  import API from '../api.js'
  export default {
    data () {
      return {
        projectid: '',
        disabled: true,
        allPatientNum: 0,
        currentPage: 1,
        currentPagePatiens: [],
        checkList: [],
        allCheck: false
      }
    },
    components: {
      DialogBox,
      MButton,
      Page
    },
    mounted () {
      this.projectid = this.$route.params.projectid
      this.currentPage = parseInt(this.$route.params.pagenum ? this.$route.params.pagenum : 1)
      this.loadPatientList()
    },
    watch: {
      '$route': function (val) {
        this.currentPage = parseInt(this.$route.params.pagenum ? this.$route.params.pagenum : 1)
        this.loadPatientList()
      },
      'checkList': function (val) {
        val.length > 0 ? this.disabled = false : this.disabled = true
      }
    },
    methods: {
      changsite () {
        this.$router.push({name: 'patientList', params: {pagenum: arguments[0]}})
      },
      // 加载该项目患者列表
      loadPatientList () {
        this.currentPagePatiens = []
        API.GetPatients({projectid: this.$route.params.projectid, pagenum: this.currentPage}).then((response) => {
          this.allPatientNum = Number(response.allnum)
          response.patientList.map(n => this.currentPagePatiens.push(n))
        }).catch((err) => {
          window.alert(err)
        })
      },
      /* 全选或全不选 */
      allChecked () {
        this.allCheck = !this.allCheck
        if (this.checkList.length !== this.currentPagePatiens.length) {
          this.checkList = []
          for (let n in this.currentPagePatiens) {
            this.checkList.push(this.currentPagePatiens[n].u_MDID)
          }
        } else {
          this.checkList = []
        }
      },
      /* 批量删除患者操作 */
      deletePatients () {
        if (this.disabled === false) {
          if (window.confirm('您确定要删除选定的患者吗？')) {
            API.DeletePatients({userid: this.$root.userid, mdid: this.checkList.join(',')}).then((response) => {
              this.loadPatientList(this.currentPage)
              this.disabled = true
              this.toast({
                type: 'success',
                text: response,
                placement: 'top'
              })
            }).catch((err) => {
              window.alert(err)
            })
          }
        }
      },
      /* 删除单个患者操作 */
      deletePatient (mdid) {
        API.DeletePatients({userid: this.$root.userid, mdid: mdid}).then((response) => {
          this.loadPatientList(this.currentPage)
        }).catch((err) => {
          window.alert(err)
        })
      }
    }
  }
</script>
<style scoped>
  /* tools */
  .tools{
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
  }
  /* 批量删除按钮 不可选中状态 */
  .tools .tools-left .disabled{
    opacity: .2;
    cursor: not-allowed;
  }
  /* 翻页按钮 */
  .tools-right a{
    display: inline;
    border: 1px solid #eee;
    padding: 0 10px;
  }
  .tools-right a.disabled{
    background: #999;
    border: 1px solid #999;
    cursor: default;
  }
  
  /* patient 展示区域*/
  .body {
    margin: 10px 0 20px;
  }
  .show-container{
    border-bottom: 1px #ccc solid;
    width: 100%;
  }
  .recordSheet, .fontBold{
    display: table;
    text-align: center;
    padding: .5em 0;
  }
  .fontBold{
    font-weight: 700;
  }

  .show-container:first-child:hover{
    background: #fff;
  }
  .show-container:hover{
    background: #ccc;
  }
  

  .patient-name{
    display: table-cell;
    width: 8em;
  }
  .patient-name a{
    color: #20a0ff;
    text-decoration: underline;
  }
  .patient-sex{
    display: table-cell;
    width: 4em;
  }
  .patient-age{
    display: table-cell;
    width: 4em;
  }
  .patient-birthday{
    display: table-cell;
    width: 8em;
  }
  .join-time{
    display: table-cell;
    width: 10em;
  }
  .update-time{
    display: table-cell;
    width: 12em;
  }
  .creater{
    display: table-cell;
    width: 8em;
  }
  .handle{
    display: table-cell;
    width: 4em;
    cursor: pointer;
    color: #f00;
  }
  
  .fontBold .handle{
    color: #000
  }
  /*分享到组body dialog*/
  .shareGroup {
    display: inline-block;
  }
  .shareGroup label{
    display: inline-block;
    padding: .3em;
    border: 1px #ccc solid;
    border-radius: 5px;
    margin: .2em;
    cursor: pointer;
  }
  .shareGroup label.checked{
    background: #20a0ff;
    border: 1px #0190fe solid;
    color: #fff;
  }
  .footer{
    width: 100%;
    text-align: right;
  }
  /* .button{
    width: 4em;
    line-height: 2em;
    display: inline-block;
    background: #bbb;    
    text-align: center;
    font-size: .9em;
    border-radius: 3px;
    color: #fff;
    margin-left: 2em;
    cursor: pointer;
  } */
</style>


