<template>
  <div>
    <Navigation></Navigation>
    <div class="list">
      <div class="option flex-row" v-for="item in noticeList">
        <div class="content">
          <span v-text="item.content"></span>
          <!--判断消息的处理状态-->
          <template v-if="item.status === '0'">
            <span class="tool" @click="handleNotice(item.logid, 1)">同意</span><span class="tool" @click="handleNotice(item.logid, 2)">拒绝</span>
          </template>
        </div>
        <div class="date">{{item.createtime | formatDate}}</div>
      </div>
    </div>
  </div>
</template>

<script>
  import API from '../api.js'
  import Navigation from '../components/navigation.vue'
  export default {
    components: {
      Navigation
    },
    data () {
      return {
        noticeList: [],
        nodispose: false
      }
    },
    created () {
      this.getNoticeList()
    },
    methods: {
      getNoticeList () {
        API.GetNotices({userid: this.$root.userid}).then(response => {
          if (!response.length) { return }
          this.noticeList = response
        }).catch(err => {
          console.log(err)
        })
      },
      handleNotice (id, status) {
        API.HandleNotice({userid: this.$root.userid, noticeid: id, status: status}).then(response => {
          if (!response) { return }
          this.getNoticeList()
        }).catch(err => {
          this.toast({
            text: err,
            type: 'error',
            placement: 'top'
          })
        })
      }
    }
  }
</script>
<style scoped>

  .list {
    width: 80%;
    margin: 0 auto;
    margin-top: 114px;
    background: #fff;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, .1);
    padding: 10px;
  }
  .option{
    justify-content: space-between;
    padding: 20px;
    border-bottom: 1px solid #eee;
    letter-spacing: 1px;
  }
  .option .content .tool{
    display: inline-block;
    padding: 0 5px;
    background: #20a0ff;
    border: 1px #0190fe solid;
    color: #fff;
    cursor: pointer;
    font-size: 14px;
  }
  .option .content .tool.active{
    display: inline-block;
    padding: 0 5px;
    background: #ccc;
    border: 1px #ccc solid;
    color: #000;
    font-size: 14px;
    cursor: default;
  }
</style>



